section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

section > h2 {
  background-color: var(--font-color);
  color: white;
  padding: 0.5rem 3rem;
  margin: 0;
  border-top: 5px var(--background-color) double;
  border-bottom: 5px var(--background-color) double;
  box-shadow: 0 8px 8px -8px var(--font-color);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 2rem;
}

section > h2 button.add {
  background-color: white;
  color: var(--font-color);
  font-size: 1rem;
  width: 1.5rem;
  height: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

section > h2 button.add:hover {
  background-color: var(--dark-background-color);
  color: var(--font-color);
}

section > * {
  padding: 0 3rem;
}

section .entry-form {
  width: 100%;
  justify-items: center;
}

section .entry-form > *:not(button):not(.buttons) {
  min-width: 28rem;
}

section .entry-form.inline, section .entry-form.short {
  justify-content: center;
}

@media (max-width: 575px) {
  section > h2 {
    justify-content: center;
    text-align: center;
  }

  section > * {
    padding: 0 0.5rem;
  }

  section .entry-form > *:not(button):not(.buttons) {
    min-width: 0;
  }
}
